.navbar{z-index:100;position:sticky;top:0;padding:0.5rem 1rem;display:grid;grid-template-columns:min-content 1fr auto;grid-template-areas:"lg sr nv";background:#f6f6f6;grid-gap:1rem;align-items:center}.navbar .sidebarToggleBtn{display:flex;align-items:center;font-size:2rem}.navbar .sidebarToggleBtn ion-icon{border-radius:3px;cursor:pointer;opacity:0.8;display:block}.navbar .sidebarToggleBtn ion-icon:hover{opacity:0.6}.navbar .sidebarToggleBtn ion-icon:active{background:white;box-shadow:0 0 0 1px rgba(0,0,0,0.2)}.navbar .navLogo{width:min-content;height:min-content}.navbar .navLogo img{width:125px;object-fit:contain}.navbar .sidebarToggleBtn+.navLogo{margin:auto;width:min-content;display:flex;justify-content:center}.navbar #searchbar{max-width:50rem;font-size:1rem}.navbar #searchbar button{right:-0.1em;padding:0.2em}.navbar #searchbar input{font-size:1rem;padding:0.55rem 1rem;padding-right:2.5rem}.navbar nav{grid-area:nv}.navbar nav .avatar{z-index:10;text-transform:uppercase;background:#0075ff;color:white;width:2.2rem;height:2.2rem;display:grid;place-items:center;position:relative;grid-template-rows:1fr}.navbar nav .avatar ion-icon{font-size:1.5rem}.navbar nav .avatar:hover{background:rgba(0,117,255,0.7)}.navbar nav .avatar section{height:0}.navbar nav .avatar .avatarOptions{position:absolute;top:35px;right:0;display:grid;grid-auto-flow:row;background:#f6f6f6;border-radius:8px;box-shadow:0 4px 8px rgba(0,0,0,0.2)}.navbar nav .avatar .avatarOptions li{display:block}.navbar nav ul{display:grid;grid-auto-flow:column;grid-column-gap:0.5rem}.navbar nav ul li{font-size:0.97rem;border-radius:5px;cursor:pointer;color:#888}.navbar nav ul li a{display:block;padding:0.5rem 0.85rem}.navbar nav ul li:hover{background:#eee}@media only screen and (max-width: 480px){.navbar{padding:0.5rem 1rem;grid-template-columns:1fr auto;grid-template-areas:"lg nv" "sr sr";grid-gap:0.5rem}.navbar.mini{display:flex;justify-content:space-between;grid-column-gap:0}.navbar.mini .navLogo{flex:0}.navbar.mini .sidebarToggleBtn+.navLogo{width:100%}.navbar #searchbar{grid-area:sr}.navbar nav{margin-left:0.5rem;grid-area:nv}}
